/*
$spacers: (
  0: 0,
  1: $spacer * 0.25,
  2: $spacer * 0.5,
  3: 27px,
  4: $spacer * 1.5,
  5: $spacer * 3,
  6: $spacer * 4,
  7: $spacer * 5,
  8: $spacer * 5.4,
  9: $spacer * 7,
  10: $spacer * 8,
);
*/
.sk-the-symbol .symbol-wrapper {
  text-align: center;
}
.sk-the-symbol .symbol-section-svg {
  width: 100%;
}
.sk-the-symbol .st0 {
  fill: none;
}
.sk-the-symbol .st1 {
  fill: #002fa7;
}
.sk-the-symbol .st2 {
  fill: #17fcc5;
}
.sk-the-symbol .st3 {
  fill: #008fc5;
}
.sk-the-symbol .st4 {
  fill: #fe0249;
}